import { Form, Button, Col, InputGroup } from "react-bootstrap/";

export default function CreatePost() {
  return (
    <Form id="create-post">
      <Form.Row>
        {/* phone number */}
        <Form.Group as={Col} md="4" controlId="validationCustom01">
          <Form.Control required type="tel" placeholder="Phone Number" />
          <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
        </Form.Group>

        {/* Name */}
        <Form.Group as={Col} md="4" controlId="validationCustom02">
          <Form.Control required type="text" placeholder="Name" />
          <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
        </Form.Group>

        {/* Location */}
        <Form.Group as={Col} md="4" controlId="validationCustomUsername">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder="Where are you"
              aria-describedby="inputGroupPrepend"
              required
            />
            <Form.Control.Feedback type="invalid">
              Please Enter Your Location
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
      </Form.Row>
      <Form.Row>
        {/* What do you want? */}
        <Form.Group as={Col} md="6" controlId="validationCustom03">
          <Form.Control
            type="text"
            placeholder="What are you looking for?"
            required
          />
          <Form.Control.Feedback type="invalid">
            Please enter What you want
          </Form.Control.Feedback>
        </Form.Group>

        {/* What are you willing to give? */}
        <Form.Group as={Col} md="6" controlId="validationCustom03">
          <Form.Control
            type="text"
            placeholder="What are you willing to give?"
            required
          />
          <Form.Control.Feedback type="invalid">
            Please enter What you are willing to give
          </Form.Control.Feedback>
        </Form.Group>
      </Form.Row>

      <Form.Row>
        {/* Attachment */}
        <Form.Group as={Col} xs="6" controlId="validationCustom03">
          <Button type="submit" block>
            Photo
          </Button>
        </Form.Group>

        {/* Submit */}
        <Form.Group as={Col} xs="6" controlId="validationCustom03">
          <Button type="submit" block>
            Submit form
          </Button>
        </Form.Group>
      </Form.Row>
    </Form>
  );
}
